<template>
	<div>
		<nav class="tania-navbar">
			<div>
				<img @click="$router.push({path:'/'})" :src="require('../assets/images/logo-tania.png')" class="logoTania goToHome" alt="">
			</div>
		</nav>
		<div class="columns" id="columns-father">
			<div class="column" :class="[project.columnClass ? project.columnClass : 'is-8 is-offset-2']" :style="{backgroundColor: project.config.bgColor}">
				<div class="columns is-multiline">
					<div class="column is-12">
						<div class="content" v-html="project.html"></div>
					</div>
					<div v-for="(image,index) in project.images" :key="index" class="column" :class="[project.imagesColumn ? project.imagesColumn : 'is-12']">
						<center>
							<img :src="image" alt="">
						</center>
					</div>
				</div>
			</div>
		</div>
		<change-project :projects="allProjects"></change-project>
	</div>
</template>
<script>
	import {getProject,getAllProjects} from '../data/projects.data';
	import changeProject from '../components/change-project.vue';
	export default{
		components:{changeProject},
		mounted(){

			let index = this.$route.params.index;
			getAllProjects().then(projects=>{
				this.allProjects = projects;
			});
			getProject(index).then(project=>{
				project.loaded();
				const {html,images,config,columnClass,imagesColumn} = project;
				this.project = {html,images, config,columnClass, imagesColumn};
				console.log(project);
			}).catch(err=>console.log(err));
		},
		data:()=>({
			project:{
				html: '',
				loaded:null,
				images:[],
				config:{bgColor: ''}
			},
			allProjects:[],
		}),
		watch:{
			'$route'(to,from){
				let index = this.$route.params.index;
				getAllProjects().then(projects=>{
					this.allProjects = projects;
				});
				getProject(index).then(project=>{
					project.loaded();
					const {html,images,config,columnClass,imagesColumn} = project;
					this.project = {html,images, config,columnClass, imagesColumn};
					console.log(project);
				}).catch(err=>console.log(err));
			}
		}
	}
</script>
<style lang="scss">
#columns-father{
	padding-bottom: 70px;
}
.columns{
	width: 100%;
	margin: 0 !important;
}
.tania-navbar{
	height: 70px;
	width: 100%;
	background-color: transparent;
	div{
		float: left;
		.logoTania.goToHome{
			height: 63px;
			width: 63px;
			margin-left: 20px;
			margin-top: 20px;
			transition-duration: 0.3s;
			cursor: pointer;
			&:hover{
				transition-duration: 0.3s;
				transform: scale(1.1) rotate(360deg);
			}
		}
	}
}
</style>



