<template>
  <div>
  	<img :src="imageBg" class="tania__top" alt="">
  	<tania-description></tania-description>
  	<br>
  	<tania-projects></tania-projects>
  </div>	
</template>

<script>
import taniaDescription from '../components/TaniaDescription';
import taniaProjects from '../components/Projects';
import imageBgImport from '../assets/top.png';
export default{
	components:{taniaDescription,taniaProjects},
	mounted(){
		document.querySelector('html').style['background'] = '#fff';
	},
	data:()=>({
		imageBg: imageBgImport
	}),
}
</script>
<style lang="scss">
	.tania__top{
		width: 100%;
	}
</style>


