<template>
	<div class="change__project__comp">
		<center>
			<div class="controls">
				<div class="previous item" @click="previousProject">
					<i class="material-icons">arrow_left</i>
				</div>
				<div class="top item" @click="goTop">
					<i class="material-icons">arrow_drop_up</i>
				</div>
				<div class="next item" @click="nextProject">
					<i class="material-icons">arrow_right</i>
				</div>
			</div>
		</center>
	</div>
</template>
<script>
	export default{
		props:['projects'],
		methods:{
			nextProject(){
				console.log(this.projects.length)
				var index = this.$route.params.index;
				if(parseInt(index) < (this.projects.length-1)){
					index = parseInt(index)+1;
					this.$router.push({path: `/project/${index}`})
				}else{
					index = 0;
					this.$router.push({path: `/project/${index}`});
				}
			},
			previousProject(){
				var index = this.$route.params.index;
				if(parseInt(index) > 0){
					this.$router.push({path: `/project/${parseInt(index)-1}`});
				}else{
					index = this.projects.length;
					this.$router.push({path: `/project/${(index - 1)}`});
				}
			},
			goTop(){
				window.scrollTo(0,0);
			}
		}
	}
</script>
<style lang="scss">
	@media screen and (max-width: 1024px){
		.change__project__comp{
			.controls{
				width: 100vw !important;
			}
		}
	}
	.change__project__comp{
		width: 100%;
		height: 70px;
		position: fixed;
		background-color: transparent;
		bottom: 0;
		.controls{
			width: 21vw;
			height: 70px;
			.item{
				-webkit-user-select: none;  /* Chrome all / Safari all */
				-moz-user-select: none;     /* Firefox all */
				-ms-user-select: none;      /* IE 10+ */
				user-select: none;          /* Likely future */
				cursor: pointer;
				width: calc(100% / 3);
				height: 70px;
				display: inline-block;
				margin-left: -3px;
				border: 0px;
				padding: 0;

				position: relative !important;
				&:hover{
					i{
						transition: 0.3s;
						font-size: 40px;
					}
				}
				i{
					transition: 0.3s;
					font-size: 30px;
					margin-top: 20px;
				}
			}
			.previous{
				background-color: rgba(255,255,255, 0.5);
				color: #000;
				border-top-left-radius: 3px;
				border-bottom-left-radius: 3px;
			}
			.top{
				background-color: rgba(0,0,0, 0.5);
				color: #fff;
			}
			.next{
				background-color: rgba(255,255,255, 0.5);
				color: #000;
				border-top-right-radius: 3px;
				border-bottom-right-radius: 3px;

			}
		}
	}
</style>



