import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/pages/Home'
import ProjectPage from '@/pages/Project.page';
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HomePage
    },{
    	path: '/project/:index',
    	name: 'ProjectPage',
    	component: ProjectPage
    }
  ]
})



