<template>
	<div class="tania__project" :style="{backgroundImage: 'url('+bg+')',height: (height-25)+'px'}">
		<div class="project__info">
			<div class="project__content" v-html="content">
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		props:{
			height:{
				type: Number,
				default: 200,
			},
			bg:{
				type: String,
				required: true,
			},
			content:{
				type: String,
				required: true,
			}			
		}
	}
</script>
<style></style>



