<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted(){
    window.addEventListener('load',()=>{
      window.scrollTo(0,0);
    })
  },
  watch:{
    '$route'(to,from){
      window.scrollTo(0,0);
    }
  }
}
</script>

<style>

</style>



