<template>
	<div>
		<section class="hero">
			<div class="hero-body">
				<div class="projects container">
					<div class="tania__title__section">
						Mis proyectos
					</div>
					<br>
					<div class="columns is-multiline">
						<div class="column is-one-quarter-desktop is-half-tablet is-one-quarter-widescreen is-full-mobile  " v-for="(project, index) in projects"  :key="index" :id="`column-${index}`"  @click="goProject(index)">
							<tania-project :bg="project.pmImage" :height="columnHeight" :content="project.description"></tania-project>
						</div>
					</div>
				</div>	
			</div>
		</section>
	</div>
</template>
<script>
	import taniaProject from './Project.vue';
	import {getAllProjects} from '../data/projects.data';
	export default{
		components:{taniaProject},
		mounted(){
			getAllProjects().then(projects=>{
				this.projects = projects;
				console.log(this.projects)
				setTimeout(()=>{
					let $column = document.getElementById('column-0');
					this.columnHeight = $column.offsetWidth;
					this.listenScroll();
				},400);
			});
		},
		data:()=>({
			columnHeight: 0,
			projects:[]
		}),
		methods:{
			listenScroll(){
				var thisOBJ = this;
				window.addEventListener('scroll',()=>{
					let $column = document.getElementById('column-1');
					thisOBJ.columnHeight = $column.offsetWidth;
				})
			},
			goProject(index){
				this.$router.push({path: `/project/${index}`});
			}
		}
	}
</script>
<style lang="scss">
	.projects{
		.tania__project{
			cursor: pointer;
			width: 100%;
			position: relative;
			// background-size: 100% 100%;
			background-size: cover;
			background-position: center !important;
			background-image: url('https://s24953.pcdn.co/blog/wp-content/uploads/2018/01/Templates-Guide-header-1.png');
			&:hover{
				.project__info
				{
					transition-duration: 0.3s;
					opacity: 1;
				}
			}
			.project__info{
				opacity: 0;
				z-index: 100;
				transition-duration: 0.3s;
				position: absolute !important;
				left: 0;
			    top: 0;
			    right: 0;
			    bottom: 0;
				width: 100%;
    			max-height: 100%;
				height: 100%;
				display: table;
				background-color: rgba(0,0,0,0.5);
				.project__content{
					display: table-cell;
					vertical-align: middle;
					color: #fff;
					text-align: center;

				}
			}
		}
	}
</style>


